.portfolio__content__wrapper{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 998;
    background: linear-gradient(106.03deg, #132324 0%, #284143 64.31%, #132223 99.74%), #FFFFFF;
    overflow: hidden;
    display: none;
    width: 100%;

    .services__content{
        padding: 0;
    }
}

.portfolio__content__wrapper.case__active{
    display: block;
}

.portfolio__content{
    overflow: auto;
    height: 100vh;
    position: relative;
    display: flex;
    flex-direction: column;
    
    &::-webkit-scrollbar{
        width: 0;
    }

    .portfolio__content__container{
        display: flex;
        flex-direction: column;
        position: relative;
        order: 1;

        ul.tabs#portfolio{
            width: 100%;
            display: flex;
            justify-content: space-between;
            margin-top: vw(50);
            order: 1;
            height: auto;
        }

        .portfolio__content__close{
            width: vw(43);
            height: vw(43);
            background: radial-gradient(50% 50% at 50% 50%, rgba(153, 233, 235, 0) 86.98%, rgba(153, 233, 235, 0.21) 100%);
            border-radius: 50%;
            cursor: pointer;
            margin-top: vw(50);
            align-self: flex-end;
            position: relative;
            z-index: 999;
            order: 2;
    
            &::after{
                content: '';
                position: absolute;
                width: vw(2.5);
                height: vw(19);
                background-color: #48CCCF;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) rotate(45deg);
            }
    
            &::before{
                content: '';
                position: absolute;
                width: vw(2.5);
                height: vw(19);
                background-color: #48CCCF;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) rotate(-45deg);
            }
        }
    
    }
    
    .tab_container{
        order: 2;

        .tab_content{

            .portfolio__content__slider{
                display: block;
            }

            .slider__wrapper{
                display: none;
            }
        }
    }

    .portfolio__footer__container{
        display: flex;
        order: 3;
        justify-content: center;
    }

    .portfolio__content__footer{
        margin-bottom: vw(50);

        .social{
            margin-bottom: vw(30);
        }

        a.back{
            cursor: pointer;
            text-align: center;
            position: relative;
    
            p{
                color: rgba($color: #fff, $alpha: 0.6);
                font-family: $MS-Regular;
                font-size: vw(16);
            }
    
            span{
                position: absolute;
                left: vw(105);
    
                &::after{
                    content: '';
                    position: absolute;
                    width: vw(2.5);
                    height: vw(12);
                    background-color: rgba($color: #fff, $alpha: 0.6);
                    transform: rotate(45deg);
                }
    
                &::before{
                    content: '';
                    position: absolute;
                    width: vw(2.5);
                    height: vw(12);
                    background-color: rgba($color: #fff, $alpha: 0.6);
                    transform: rotate(135deg);
                    bottom: vw(-18);
                }
            }
        }
    }
}

.portfolio__content__slider{
    position: relative;

    .pagination__wrapper{
        position: absolute;
        top: vw(130);
        left: 50%;
        transform: translateX(-50%);

        .swiper-pagination-content{
            width: vw(410);
            height: vw(2);
            background-color: rgba(#fff, .2);
            display: flex;
            align-items: center;
            justify-content: center;

            .swiper-pagination-bullet{
                width: 40%;
                height: vw(2);
                cursor: pointer;

                &.swiper-pagination-bullet-active{
                    background-color: rgba(#fff, .5);
                }
            }
        }

        .counter-content{
            position: relative;

            .counter__current{
                position: absolute;
                left: vw(-50);
                bottom: vw(-5);
                font-size: vw(24);
                color: #656565;
                font-family: $DTW-Medium;
            }

            .counter__total{
                font-size: vw(15);
                color: #4D4D4D;
                font-family: $DTW-Medium;
                position: absolute;
                bottom: vw(5);

                span{
                    font-family: $MS-SemiBold;
                }
            }
        }
    }
}

.portfolio__content__arrows{
    position: absolute;
    align-self: center;
    width: vw(943);
    display: flex;
    justify-content: space-between;
    margin-top: vw(50);
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    order: 3;
    z-index: 999;

    .swiper-button-content-prev, .swiper-button-content-next{
        position: relative;
        cursor: pointer;
        width: vw(74);
        height: vw(74);
        z-index: 999;

        &.disable{
            opacity: .5;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &::before{
            content: '';
            position: absolute;
            inset: 0;
            border-radius: 50%;
            padding: vw(3);
            width: vw(60);
            height: vw(60);
            background: radial-gradient(50% 50% at 50% 50%, rgba(153, 233, 235, 0) 86.98%, rgba(153, 233, 235, 0.21) 100%);
            -webkit-mask: 
                linear-gradient(white 0 0) content-box, 
                linear-gradient(white 0 0);
            -webkit-mask-composite: xor;
            mask-composite: exclude;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .btn__arrow{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            .btn__img{
                width: vw(8);
                height: vw(14);

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
    }

    .swiper-button-content-prev{

        img{
            transform: rotate(180deg);
        }
    
        .btn__arrow{
            .btn__img{
                img{
                    transform: rotate(0deg);
                }
            }
        }
    }
}

.portfolio__content__slider{

    .swiper-slide{
        height: auto;
        z-index: auto;
        padding-top: vw(190);
        
        &::after{
            display: none;
        }
    }

    .swiper{
        margin-top: 0;
    }
}

