    align-items: center;
}

.smm__images__stats{
    padding: vw(45);
    background: rgba(255, 255, 255, 0.08);
    box-shadow: 0px vw(9) vw(14) rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(vw(16));
    border-radius: vw(14);
    position: absolute;
    z-index: 999;

    &.--bottom__center__stats{
        left: 50%;
        transform: translateX(-50%);
        top: calc(100% + vw(-30));
    }

    &.--bottom__left__stats{
        left: vw(20);
        top: /* calc(100% + vw(-30)) */100%;
    }

    &.--center__right__stats{
        top: calc(50% - vw(250));
        transform: translateY(-50%);
        left: calc(-100% + vw(40));
    }

    &::before{
        content: '';
        position: absolute;
        inset: 0;
        border-radius: vw(14);
        padding: vw(1);
        background: linear-gradient(179.22deg, rgba(175, 253, 255, 0) 0.7%, #AFFDFF 52.82%, rgba(175, 253, 255, 0) 100.77%),
        linear-gradient(89.92deg, rgba(175, 253, 255, 0) -1.09%, #AFFDFF 51.52%, rgba(175, 253, 255, 0) 99.92%);
        -webkit-mask: 
            linear-gradient(white 0 0) content-box, 
            linear-gradient(white 0 0);
        -webkit-mask-composite: xor;
        mask-composite: exclude;
    }

    p.text{
        font-size: vw(48);
        color: #08C0C5;
        font-family: $MS-SemiBold;
    }

    p.title{
        font-size: vw(20);
        color: rgba(#fff, .68);
        font-family: $MS-Regular;
    }

    .stats__list__wrapp{
        position: relative;
        padding-left: vw(25);
        width: max-content;

        &::after{
            content: '';
            position: absolute;
            width: vw(1.5);
            height: calc(100% + vw(5));
            background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.16) 53.53%, rgba(255, 255, 255, 0) 101.75%);
            top: 50%;
            transform: translateY(-50%);
            left: 0;
        }

        li{
            font-size: vw(20);
            font-family: $MS-Regular;
            color: rgba(#fff, 0.68);
            margin-top: vw(15);
            position: relative;

            &::after{
                content: '';
                position: absolute;
                width: vw(27);
                height: vw(7);
                background: radial-gradient(50% 50% at 50% 50%, rgba(0, 255, 255, 0.94433) 3.12%, #00FFFF 6.77%, rgba(0, 255, 255, 0.814433) 15.62%, rgba(0, 255, 255, 0.34) 25%, rgba(0, 255, 255, 0.1) 50.52%, rgba(0, 255, 255, 0) 97.92%);
                top: vw(7);
                transform: rotate(-90deg);
                left: vw(-37.6);
            }

            &:first-child{
                margin-top: 0;
            }

            span{
                font-family: $MS-SemiBold;
            }
        }
    }
}

.smm__images__stats__wrapp{
    position: relative;
    margin-bottom: vw(150);
}

@media (max-width: 990px){
    .smm__wrapper{
        width: 100%;
        padding: 0 vmin(35);

        p.extremist{
            font-size: vmin(13);
            margin-top: vmin(20);
            margin-left: 0;

            br{
                display: none;
            }
        }
    }

    .smm__info{
        padding-left: 0;
    
        &::after{
            display: none;
        }
    }

    .smm__info__item{
        margin-top: vmin(40);
    
        &::after{
            display: none;
        }
    
        &:first-child{
            margin-top: vmin(40);
        }
    
        p.title{
            font-size: vmin(16);
        }
    
        p.text{
            font-size: vmin(13);
            margin-top: vmin(25);
        }

        .smm__info__list{
            margin-top: vmin(25);
        
            .smm__info__list{
                margin-top: 0;
        
                li{
                    padding-left: vmin(30);
        
                    &::after{
                        left: vmin(20);
                    }
                }
            }
        
            li{
                padding-left: vmin(15);
                font-size: vmin(13);
                line-height: 150%;
        
                &::after{
                    width: vmin(4);
                    height: vmin(4);
                    top: vmin(8);
                    left: vmin(5);
                }
            }
        }
    }

    .before__after__wrapp{
        align-items: center;
        flex-direction: column;
        column-gap: 0;
        margin-top: vmin(60);
    
        .smm__item{
            margin-top: vmin(150);

            &:first-child{
                margin-top: 0;
            }
        }
    
        .before__after__line{
            width: vmin(1);
            height: vmin(120);
    
            &::before{
                width: vmin(5);
                height: vmin(5);
                top: 0;
                transform: translateY(0);
                left: 50%;
                transform: translateX(-50%);
            }
    
            &::after{
                content: '';
                position: absolute;
                width: vmin(5);
                height: vmin(5);
                top: auto;
                bottom: 0;
                right: auto;
                transform: translateY(0);
                left: 50%;
                transform: translateX(-50%);
                box-shadow: 0px 0px vmin(2) rgba(175, 253, 255, 0.55);
            }
        }
    }

    .smm__item{
        display: flex;
        flex-direction: column;
        margin-top: vmin(60);
    
        p.smm__title{
            font-size: vmin(16);
            margin-bottom: vmin(20);
        }

        .smm__images{
            width: vmin(356);

            img{
                width: 100%;
                height: 100%;
                object-fit: 100%;
            }
            
            /* &.big-img{
                width: vmin(356);
                height: vmin(479);

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            } */
        }
    }

    .smm__images{
        position: relative;
    
        .smm__images__stats{
            position: absolute;
        }
    
        &.phone{
            width: vmin(304);
            height: vmin(611);
        }
    }
    
    .smm__item__row{
        flex-direction: column;
        row-gap: vw(20);
        .smm__images{
            img{
                width: 100%;
                height: 100%;
                object-fit: 100%;
            }
        }
        
    }

    .smm__item__column{
        column-gap: 0;

        .smm__images{

            img{
                width: 100%;
                height: 100%;
                object-fit: 100%;
            }
        }
    }

    .smm__images__stats{
        padding: vmin(30);
        background: rgba(255, 255, 255, 0.08);
        box-shadow: 0px vmin(9) vmin(14) rgba(0, 0, 0, 0.2);
        backdrop-filter: blur(vmin(16));
        border-radius: vmin(14);

        &.--bottom__left__stats{
            left: 50%;
            transform: translateX(-50%);
        }

        &.--center__right__stats{
            top: 100%;
            transform: translateY(0);
            left: 50%;
            transform: translateX(-50%);
        }

        p.text{
            font-size: vmin(20);
        }
    
        p.title{
            font-size: vmin(13);
        }
    
        &.--bottom__center__stats{
            top: calc(100% + vmin(-15));
        }
    
        &::before{
            border-radius: vmin(14);
            padding: vmin(1);
        }
    
        .stats__list__wrapp{
            padding-left: vmin(25);
    
            &::after{
                width: vmin(1.5);
                height: calc(100% + vmin(5));
            }
    
            li{
                width: max-content;
                font-size: vmin(13);
                font-family: $MS-Regular;
                color: rgba(#fff, 0.68);
                margin-top: vmin(10);
                position: relative;
    
                &::after{
                    width: vmin(27);
                    height: vmin(6);
                    top: vmin(5);
                    left: vmin(-37.6);
                }
            }
        }
    }

    .smm__images__stats__wrapp{
        position: relative;
        margin-bottom: vmin(150);
    }
}