.quiz{
    z-index: 9999;
    width: vw(1172);
    height: vw(641);
    top: vw(100);
    background: rgba(255, 255, 255, 0.08);
    box-shadow: 0px vw(51) vw(74) rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(vw(32));
    border-radius: vw(41);
    display: flex;
    flex-direction: column;
    padding: vw(50) vw(120);

    &::before{
        content: '';
        position: absolute;
        inset: 0;
        border-radius: vw(41);
        padding: vw(1);
        background: linear-gradient(179.22deg, rgba(175, 253, 255, 0) 0.7%, #AFFDFF 52.82%, rgba(175, 253, 255, 0) 100.77%),
        linear-gradient(89.92deg, rgba(175, 253, 255, 0) -1.09%, #AFFDFF 51.52%, rgba(175, 253, 255, 0) 99.92%);
        -webkit-mask: 
            linear-gradient(white 0 0) content-box, 
            linear-gradient(white 0 0);
        -webkit-mask-composite: xor;
        mask-composite: exclude;
    }
}

.uacf7-prev, .uacf7-next{
    position: relative;
    z-index: 100;
}

p.quiz__title{
    font-size: vw(20);
    text-align: center;
    font-family: $MS-Regular;
    line-height: 140%;
    color: rgba($color: #FFFFFF, $alpha: 0.8);
    span.mob{
        display: none;
    }
}

p.quiz__answer__num{
    font-size: vw(20);
    text-align: center;
    font-family: $MS-Regular;
    color: rgba($color: #FFFFFF, $alpha: 0.8);
    padding-top: vw(25);
}

.quiz__pagination{
    width: 100%;
    padding-left: vw(150);
    padding-right: vw(150);
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: vw(2);
    margin-top: vw(40);
    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.16) 53.53%, rgba(255, 255, 255, 0) 101.75%);
    
    .quiz__pag__item{
        cursor: pointer;
        width: vw(53.3);
        height: vw(16);
        background: radial-gradient(50% 50% at 50% 50%, rgba(146, 175, 175, 0.94433) 3.12%, #92AFAF 6.77%, rgba(146, 175, 175, 0.814433) 15.62%, rgba(146, 175, 175, 0.34) 25%, rgba(146, 175, 175, 0.1) 50.52%, rgba(146, 175, 175, 0) 66.15%);
        
        &.active{
            background: radial-gradient(50% 50% at 50% 50%, rgba(0, 255, 255, 0.94433) 3.12%, #00FFFF 6.77%, rgba(0, 255, 255, 0.814433) 15.62%, rgba(0, 255, 255, 0.34) 25%, rgba(0, 255, 255, 0.1) 50.52%, rgba(0, 255, 255, 0) 97.92%);
        }
    }

    z-index: 9999;
}

.quiz__btn__wrapper{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-self: flex-end;

    button.btn__desc{
        background: transparent;
        outline: none;
        border: none;
        cursor: pointer;
    }

    .btn__arrow__stroke{
        position: relative;
        cursor: pointer;
        width: vw(74);
        height: vw(74);
        z-index: 9999;

        .submit__form{
            width: vw(74);
            height: vw(74); 
            background: transparent;
            color: transparent;
            outline: none;
            border: none;
            cursor: pointer;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 99999;
        }

        &:first-child{
            img{
                transform: rotate(180deg);
            }

            .btn__arrow{
                .btn__img{
                    img{
                        transform: rotate(0deg);
                    }
                }
            }
        }

        &.disable{
            opacity: .5;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &::before{
            content: '';
            position: absolute;
            inset: 0;
            border-radius: 50%;
            padding: vw(2);
            width: vw(60);
            height: vw(60);
            background: radial-gradient(50% 50% at 50% 50%, rgba(153, 233, 235, 0) 86.98%, rgba(153, 233, 235, 0.21) 100%);
            -webkit-mask: 
                linear-gradient(white 0 0) content-box, 
                linear-gradient(white 0 0);
            -webkit-mask-composite: xor;
            mask-composite: exclude;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .btn__arrow{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            .btn__img{
                width: vw(8);
                height: vw(14);

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
    }
}

.quiz__page__content{
    position: relative;
    z-index: 9999;

    .quiz__page__title{
        text-align: center;
        font-size: vw(30);
        font-family: $DTW-Bold;
        padding-top: vw(5);
        line-height: 120%;
        color: white;
    }

    .quiz__page__text{
        font-size: vw(16);
        font-family: $MS-Regular;
        color: rgba(#fff, 0.8);
        text-align: center;
        padding-top: vw(10);
    }
}

.quiz__input__body{
    display: flex;
    align-items: center;

    p.quiz__input__text{
        font-size: vw(20);
        font-family: $MS-Regular;
        color: white;
    }

    .quiz__input{
        background-color: rgba(#fff, 0.12);
        outline: none;
        border: vw(1.5) solid #BFE9EB;
        border-radius: vw(20);
        font-size: vw(18);
        font-family: $MS-Regular;
        color: white;
        padding: 0 vw(35);
    }
}

.quiz__checkbox__wrapper{
    margin: vw(25) 0;
    position: relative;

    .quiz__checkbox__body{
        width: vw(115);
        label{
            display: flex;
            flex-direction: column;
            align-items: center;
            
            input{
                position: relative;
                cursor: pointer;
                width: vw(58);
                height: vw(53);
                appearance: none;
    
                &::after{
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background-color: rgba(#fff, 0.12);
                    left: 0;
                    top: 0;
                    border: vw(1.5) solid #BFE9EB;
                    border-radius: vw(20);
                }
                
                &:checked::before {
                    content: '';
                    position: absolute;
                    width: vw(23);
                    height: vw(23);
                    background: linear-gradient(180deg, #00FFFF 15.88%, #249396 115.88%);
                    box-shadow: 0px 0px vw(30) #AFFDFF;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    border-radius: 50%;
                }
            }
            
            .wpcf7-list-item-label{
                font-size: vw(16);
                font-family: $MS-Regular;
                color: white;
                line-height: 120%;
                text-align: center;
                margin-top: vw(20);
            }
        }
    }
}

#step0{

    .quiz__input__body{
        justify-content: space-between;
        margin-top: vw(20);

        .quiz__input{
            width: vw(575);
            height: vw(56);
        }
    }
}

#step1{

    .quiz__input__body{

        .quiz__input{
            width: vw(575);
            height: vw(53);
            margin-left: vw(50);
        }
    }

    .quiz__checkbox__wrapper{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
}

#step2{

    .quiz__checkbox__wrapper{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .quiz__checkbox__wrapper{
        margin: vw(25) 0;
        position: relative;
    
    }

    .btn__decide{
        display: flex;
        justify-content: center;

        .wpcf7-form-control-wrap{
            position: absolute;
            top: 0;
            left: 0;
        }
        
        label{
            display: inline-block;
            width: vw(339);
            height: vw(64);
            cursor: pointer;
            user-select: none;
            
            input{
                appearance: none;
            }
            
            .wpcf7-list-item-label{
                color: transparent;
            }
        }
    }
}

#step3{

    .quiz__checkbox__wrapper{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        justify-content: space-between;
        align-self: center;
    }

    .quiz__checkbox__wrapper{
        margin: vw(25) 0;
        position: relative;
        column-gap: vw(20);
        row-gap: vw(30);

        .quiz__checkbox__body{
            width: auto;
            label{
                display: flex;
                flex-direction: row;
                align-items: center;
                
                input{
                    position: relative;
                    cursor: pointer;
