                        border-radius: vw(20);
                    }
                    
                    &:checked::before {
                        content: '';
                        position: absolute;
                        width: vw(23);
                        height: vw(23);
                        background: linear-gradient(180deg, #00FFFF 15.88%, #249396 115.88%);
                        box-shadow: 0px 0px vw(30) #AFFDFF;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        border-radius: 50%;
                    }
                }
                
                .wpcf7-list-item-label{
                    width: vw(253);
                    margin-top: 0;
                    text-align: left;
                    margin-left: vw(20);
                }
            }
        }
    }
}

#step4{

    .quiz__page__title{
        padding-bottom: vw(20);
    }

    .quiz__input__wrapper{
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    
    .quiz__input__body{
        margin: 0 auto;
        margin-top: vw(25);

        .quiz__input__text.mobtext{
            display: none;
        }

        input{
            width: vw(699);
            height: vw(78);
            color: white;

            &::placeholder{
                color: white;
            }
        }
    }
}

#step5{

    .quiz__switch__wrapper{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: vw(50);
    
        .quiz__switch__item{
            width: vw(153);
            height: vw(131);
            border-radius: vw(16);
            position: relative;
            margin-left: vw(50);
            cursor: pointer;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            .wpcf7-form-control-wrap{
                position: absolute;
                top: 0;
                left: 0;
                width: vw(114);
                height: vw(103);
    
                .wpcf7-list-item{
                    margin: 0;
                }
    
                input[type="checkbox"]{
                    width: vw(114);
                    height: vw(103);
                    appearance: none;
                }
            
                .wpcf7-list-item-label{
                    display: none;
                }
            }

            &.active{
                background: linear-gradient(321.97deg, #12989B 25.19%, #07C3C8 84.94%);
            }

            p{
                margin-top: vw(10);
                font-size: vw(20);
                font-family: $MS-Regular;
                color: white;
            }

            .qsi__img{

                &:nth-child(1){
                    width: vw(61);
                    height: vw(60);
                }

                &:nth-child(2){
                    width: vw(54);
                    height: vw(45);
                }

                &:nth-child(3){
                    width: vw(49);
                    height: vw(55);
                }

                &:nth-child(4){
                    width: vw(50);
                    height: vw(50);
                }

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            &:first-child{
                margin-left: 0;
            }

            &::before{
                content: '';
                position: absolute;
                inset: 0;
                border-radius: vw(16);
                padding: vw(1);
                background: linear-gradient(179.22deg, rgba(175, 253, 255, 0) 0.7%, #AFFDFF 52.82%, rgba(175, 253, 255, 0) 100.77%),
                linear-gradient(89.92deg, rgba(175, 253, 255, 0) -1.09%, #AFFDFF 51.52%, rgba(175, 253, 255, 0) 99.92%);
                -webkit-mask: 
                    linear-gradient(white 0 0) content-box, 
                    linear-gradient(white 0 0);
                -webkit-mask-composite: xor;
                mask-composite: exclude;
            }
        }
    }
}

