                        border-radius: vmin(20);
                    }
                    
                    &:checked::before {
                        width: vmin(17);
                        height: vmin(17);
                        box-shadow: 0px 0px vmin(30) #AFFDFF;
                    }
                }
                
                .wpcf7-list-item-label{
                    font-size: vmin(12);
                    margin-top: vw(20);
                }
            }
        } 
    }

    #step1{

        .quiz__input__body{
    
            .quiz__input{
                width: 100%;
                height: vmin(39);
                margin-left: 0;
            }
        }
    
        .quiz__checkbox__wrapper{
            display: grid;
            grid-template-columns: vmin(111) vmin(111) vmin(111);
            justify-content: center;
            align-items: none;
            row-gap: vmin(20);
        }
    }

    #step2{

        .quiz__checkbox__wrapper{
            display: grid;
            grid-template-columns: vmin(100) vmin(100) vmin(100);
            justify-content: center;
            align-items: none;
            row-gap: vmin(15);
        }
    
        .quiz__checkbox__wrapper{
            margin: vmin(30) 0;
            position: relative;
        }
    
        .btn__decide{
            
            label{
                width: vmin(284);
                height: vmin(49);
            }
        }

        .btn.--not__decide{
            width: vmin(284);
            height: vmin(49);
            font-size: vmin(12);
            margin-top: vmin(20);
        }
    }

    #step3{

        .quiz__checkbox__wrapper{
            display: grid;
            grid-template-columns: vmin(100) vmin(100) vmin(100);
            justify-content: center;
            align-items: none;
            row-gap: vmin(15);
        }
    
        .quiz__checkbox__wrapper{
            margin: vmin(70) 0;
            position: relative;

            .quiz__checkbox__body{
                width: vmin(100);
                label{
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    
                    input{
                        position: relative;
                        cursor: pointer;
                        width: vmin(39);
                        height: vmin(39);
                        appearance: none;
            
                        &::after{
                            border: vmin(1.5) solid #BFE9EB;
                            border-radius: vmin(20);
                        }
                        
                        &:checked::before {
                            width: vmin(17);
                            height: vmin(17);
                            box-shadow: 0px 0px vmin(30) #AFFDFF;
                        }
                    }
                    
                    .wpcf7-list-item-label{
                        font-size: vmin(12);
                        margin-top: vw(20);

                        span{
                            display: none;
                        }
                    }
                }
            } 
        }

    }

    #step4{

        .quiz__page__title{
            padding-bottom: vmin(20);
        }
    
        .quiz__input__wrapper{
            display: flex;
            justify-content: center;
            flex-direction: column;
        }
        
        .quiz__input__body{
            margin: 0 auto;
            margin-top: vmin(25);

            .quiz__input__text.mobtext{
                display: block;
            }
    
            input{
                width: vmin(306);
                height: vmin(47);
                color: white;
    
                &::placeholder{
                    color: transparent;
                }
            }
        }
    }

    #step5{

        .quiz__switch__wrapper{
            display: grid;
            grid-template-columns: vmin(103) vmin(103);
            justify-content: space-between;
            align-items: center;
            margin-top: vmin(30);
            padding: 0 vmin(30);
            row-gap: vmin(20);
        
            .quiz__switch__item{
                width: vmin(103);
                height: vmin(88);
                border-radius: vmin(16);
                margin-left: 0;

                .wpcf7-form-control-wrap{
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: vmin(103);
                height: vmin(88);
        
                    .wpcf7-list-item{
                        margin: 0;
                    }
        
                    input[type="checkbox"]{
                        width: vmin(103);
                        height: vmin(88);
                        appearance: none;
                    }
                
                    .wpcf7-list-item-label{
                        display: none;
                    }
                }
    
    
                p{
                    margin-top: vmin(5);
                    font-size: vmin(13);
                }
    
                .qsi__img{
    
                    &:nth-child(1){
                        width: calc(vmin(61) - 25%);
                        height: calc(vmin(60) - 25%);
                    }
    
                    &:nth-child(2){
                        width: calc(vmin(54) - 25%);
                        height: calc(vmin(45) - 25%);
                    }
    
                    &:nth-child(3){
                        width: calc(vmin(49) - 25%);
                        height: calc(vmin(55) - 25%);
                    }
    
                    &:nth-child(4){
                        width: calc(vmin(50) - 25%);
                        height: calc(vmin(50) - 25%);
                    }
                }
    
                &::before{
                    border-radius: vmin(16);
                    padding: vmin(1);
                }
            }
        }
    }

